import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const AllNotebooks = React.lazy(() => import('./notebook-table'));
const CreateNotebooks = React.lazy(() => import('./notebook-create'));

const Notebook = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Route
        path={`${match.url}/createWorkspace`}
        render={(props) => <CreateNotebooks {...props} />}
      />
      <Route
        path={`${match.url}/`}
        render={(props) => <AllNotebooks {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default Notebook;
